import { createWebHistory, createRouter } from "vue-router";
import listCompo from './components/listCompo'
import homeCompo from './components/homeCompo'
import detailCompo from './components/detailCompo'
const routes = [
    {
      path: "/",
      component: homeCompo,
    },
    {
    path: "/list",
    component: listCompo,
    },
    {
      path: "/detail/:id", //(\\d+) is limiting input for only decimal
      component: detailCompo,
    },
    {
      path: "/:anything(.*)",
      component: homeCompo,
    },

];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 